.tooltip__button {
    width: 40px;
    height: 40px;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    background-image: url(../../../images/Close-Icon.png);
    background-size: 40px 40px;
    margin-left: 100%;
    position: absolute;
    top: -40px;
    left: -5px;
    transition: opacity 1s ease-in-out;
}

.tooltip__button:hover {
    opacity: .6;
    cursor: pointer;
}

@media (max-width: 730px) {
    .tooltip__button {
        width: 26px;
        height: 26px;
        background-size: 26px 26px;
        top: -40px;
        left: -25px;
    }
}